.profile {
	&__container {
		display: block;
		text-decoration: none;
		@include clr; }
	&__figure {
		float: left;
		width: 64px;
		height: 64px;
		@include vertical; }
	&__logo,
	&__img {
		display: inline-block;
		vertical-align: middle;
		background-position: center center;
		background-size: cover; }
	&__logo {
		font-size: 46px; }
	&__img {
		border-radius: 50%; }
	&__title {
		color: $white; }
	&__meta {
		color: $white; } }
